import axios from 'axios'
const url = '';

/*
    api always returns promise
 */

const api = {
    getFilms(request) {
        return axios.get(`${url}/api/film`, { params: request})
    },
    getFilmById(id) {
        return axios.get(`${url}/api/film/${id}`)
    },
    putFilm(data) {
        return axios.put(`${url}/api/film`, data)
    },
    deleteFilmById(id) {
        return axios.delete(`${url}/api/film/${id}`)
    },
};

export default api;
