import React from 'react';
import { withStyles } from "@material-ui/core";
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import AddIcon from '@material-ui/icons/Add';
import Fab from '@material-ui/core/Fab';

const styles = theme => ({
    invisible: {
        display: 'None',
    },
});

class AddFilmDialog extends React.Component{
    constructor(props) {
        super(props);
        this.state = {
            open: false,
            film: {
                title: '',
                release_date: '',
                format: '',
                actors: '',
            },
        }
    }
    handlerInputTitle = (e) => {
        const { film } = this.state;
        this.setState({
            film: {...film, title: e.target.value}
        })
    };
    handlerInputDate = (e) => {
        const { film } = this.state;
        this.setState({
            film: {...film, release_date: e.target.value}
        })
    };
    handlerInputFormat = (e) => {
        const { film } = this.state;
        this.setState({
            film: {...film, format: e.target.value}
        })
    };
    handlerInputActors = (e) => {
        const { film } = this.state;
        this.setState({
            film: {...film, actors: e.target.value}
        })
    };
    handlerOpen = () => {
        const open = this.state.open;
        this.setState({
            open: !open,
        })
    };
    handlerCancel = () => {
        this.setState({
            film: {
                title: '',
                release_date: '',
                format: '',
                actors: '',
            },
            open: false,
        })
    };
    handlerSubmit = async () => {
        await this.props.handlerAddFilm(this.state.film);
        this.handlerCancel();
    };
    handlerButtonFileInput = () => {
        document.getElementById('dialog_file_input').click();
    };
    handlerFileInput = () => {
        let data = document.getElementById('dialog_file_input').files;
        data = data[data.length - 1];
        const fileReader = new FileReader();
        fileReader.readAsText(data);
        fileReader.onload = (e) => {
            const rows = e.target.result.split('\n');
            let data = [];
            let aPieceOfData = {};
            rows.forEach(row => {
                if (row.length > 1) {
                   let [prop, value] = row.split(': ');
                   if (prop === 'Stars') aPieceOfData.actors = value;
                   else if (prop === 'Release Year') aPieceOfData.release_date = value;
                   else aPieceOfData[prop.toLowerCase()] = value;
                }
                else {
                    aPieceOfData.title ? data.push(aPieceOfData) : aPieceOfData = {};
                    aPieceOfData = {};
                }
            });
            this.props.handlerMassiveAddFilms(data);
            this.handlerCancel();
        };
    };
    render() {
        const { classes } = this.props;
        return (
            <div>
                <Fab onClick={this.handlerOpen} color="secondary" aria-label="add">
                    <AddIcon />
                </Fab>
                <Dialog open={this.state.open} onClose={this.handlerOpen}>
                    <DialogTitle>
                        Add film
                    </DialogTitle>
                    <DialogContent>
                        <TextField
                            required
                            autoFocus
                            margin="dense"
                            label="Title"
                            value={this.state.film.title}
                            onChange={this.handlerInputTitle}
                            fullWidth/>
                        <TextField
                            autoFocus
                            margin="dense"
                            label="Release Date"
                            type="date"
                            value={this.state.film.release_date}
                            onChange={this.handlerInputDate}
                            InputLabelProps={{
                                shrink: true,
                            }}
                            fullWidth/>
                        <TextField
                            margin="dense"
                            label="Format"
                            value={this.state.film.format}
                            onChange={this.handlerInputFormat}
                            fullWidth/>
                        <TextField
                            margin="dense"
                            label="Stars"
                            value={this.state.film.actors}
                            onChange={this.handlerInputActors}
                            fullWidth/>
                    </DialogContent>
                    <DialogActions>
                        <Button onClick={this.handlerCancel} color="secondary">
                            Cancel
                        </Button>
                        <input type="file" accept=".txt" className={classes.invisible}
                               id="dialog_file_input" onInput={this.handlerFileInput}/>
                        <Button color="primary" onClick={this.handlerButtonFileInput}>
                            Upload from file
                        </Button>
                        <Button onClick={this.handlerSubmit} color="primary">
                            Submit
                        </Button>
                    </DialogActions>
                </Dialog>
            </div>
        )
    }
}

export default withStyles(styles)(AddFilmDialog)
