import React from 'react';
import { withStyles } from "@material-ui/core";
import Grid from "@material-ui/core/Grid"
import SearchIcon from "@material-ui/icons/Search";
import InputBase from "@material-ui/core/InputBase";
import {fade} from "@material-ui/core/styles";
import FormControl from "@material-ui/core/FormControl";
import InputLabel from "@material-ui/core/InputLabel";
import Select from "@material-ui/core/Select";
import MenuItem from "@material-ui/core/MenuItem";
import debounce from "lodash/debounce";

const styles = theme => ({
    search: {
        position: 'relative',
        display: 'flex',
        alignItems: 'center',
        flexGrow: 1,
        borderRadius: theme.shape.borderRadius,
        backgroundColor: fade(theme.palette.common.white, 0.15),
        '&:hover': {
            backgroundColor: fade(theme.palette.common.white, 0.25),
        },
        marginRight: theme.spacing(2),
        marginLeft: 0,
        width: '100%',
        [theme.breakpoints.up('sm')]: {
            marginLeft: theme.spacing(3),
            width: 'auto',
        },
    },
    searchIcon: {
        width: theme.spacing(7),
        height: '100%',
        pointerEvents: 'none',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
    },
    inputRoot: {
        color: 'inherit',
        flexGrow: 1,
    },
    inputInput: {
        padding: theme.spacing(1, 1, 1, 0),
        transition: theme.transitions.create('width'),
        width: '100%',
    },
    searchSelect: {
        flexGrow: 0.1,
    }
});

class Search extends React.Component {
    constructor(props) {
        super(props);
        this.searchFilms = debounce(this.props.handlerSearch, 100);
        this.state = {
            query: {
                type: 'title',
                query: '',
                order: 'DESC',
            }
        }
    }
    handleSelectType = (e) => {
        let { query } = this.state;
        query.type = e.target.value;
        this.setState({
            query,
        });
        this.searchFilms(this.state.query);
    };
    handleSelectOrder = (e) => {
        let { query } = this.state;
        query.order = e.target.value;
        this.setState({
            query,
        });
        this.searchFilms(this.state.query);
    };
    handleSearchInput = (e) => {
        let { query } = this.state;
        query.query = e.target.value;
        this.setState({
            query
        });
        this.searchFilms(this.state.query);
    };
    render() {
        const { classes } = this.props;
        return (
            <Grid container>
                <div className={classes.search}>
                    <div className={classes.searchIcon}>
                        <SearchIcon />
                    </div>
                    <InputBase
                        placeholder="Search…"
                        value={this.state.query.query}
                        classes={{
                            root: classes.inputRoot,
                            input: classes.inputInput,
                        }}
                        inputProps={{ 'aria-label': 'search' }}
                        onChange={this.handleSearchInput}
                    />
                </div>
                <FormControl className={classes.searchSelect}>
                    <InputLabel htmlFor="search-by">Search by</InputLabel>
                    <Select
                        onChange={this.handleSelectType}
                        value={this.state.query.type}
                        displayEmpty
                    >
                        <MenuItem value="title">title</MenuItem>
                        <MenuItem value="actors">actor</MenuItem>
                    </Select>
                </FormControl>
                <FormControl className={classes.searchSelect}>
                    <InputLabel htmlFor="search-by">Order</InputLabel>
                    <Select
                        onChange={this.handleSelectOrder}
                        value={this.state.query.order}
                        displayEmpty
                    >
                        <MenuItem value="ASC">ascending</MenuItem>
                        <MenuItem value="DESC">descending</MenuItem>
                    </Select>
                </FormControl>
            </Grid>
        )
    }
}

export default withStyles(styles)(Search)

