import React from 'react';
import { withStyles } from "@material-ui/core";
import Container from "@material-ui/core/Container";
import Grid from '@material-ui/core/Grid';

import api from '../api';
import filmValidator from '../utils/filmValidator';

import Film from './Film';
import AddFilmDialog from './AddFilmDialog';
import Search from './Search';


const styles = theme => ({
    container: {
        margin: theme.spacing(1),
    },
    film: {
        margin: theme.spacing(2),
    }
});

class Home extends React.Component{
    constructor(props) {
        super(props);
        this.state = {
            films: [{
                title: '',
                release_date: new Date(),
                format: '',
                id: 0,
                actors: ''
            }],
        }
    }
    componentDidMount() {
        api.getFilms('').then(res => {
            this.setState({
                films: res.data,
            })
        })
    }
    handlerSearch = async (request) => {
        const res = await api.getFilms(request);
        this.setState({
            films: res.data,
        })
    };
    handlerAddFilm = async (film) => {
        const { data } = await api.putFilm(film);
        const films = this.state.films;
        this.setState({
            films: [...films, data]
        })
    };
    handlerDeleteFilm = async (filmId) => {
        await api.deleteFilmById(filmId);
        const indexOfFilm = this.state.films.findIndex(el => el.id === filmId);
        const newFilms = this.state.films.slice(0, indexOfFilm)
            .concat(this.state.films.slice(indexOfFilm + 1, this.state.films.length));
        this.setState({
            films: newFilms,
        });
    };
    handlerMassiveAddFilms = async (films) => {
        let filmPutPromises = [];
        for (let film of films) {
            filmValidator(film) ? filmPutPromises.push(api.putFilm(film)) : console.log('Wrong film data');
        }
        let newFilms = await Promise.all(filmPutPromises);
        newFilms = newFilms.map(res => res.data);
        const previousFilms = this.state.films;
        this.setState({
            films: [...previousFilms, ...newFilms]
        });
    };
    render() {
        const { classes } = this.props;
        return (
            <Container>
                <Search handlerSearch={this.handlerSearch}/>
                {this.state.films.map(film =>
                    <div key={film.id} className={classes.film}>
                        <Film film={film}
                              handlerDeleteFilm={this.handlerDeleteFilm}/>
                    </div>)}
                <Grid container justify="center" className={classes.container}>
                    <AddFilmDialog
                        handlerAddFilm={this.handlerAddFilm}
                        handlerMassiveAddFilms={this.handlerMassiveAddFilms}/>
                </Grid>
            </Container>
        )
    }
}

export default withStyles(styles)(Home);
