import React from 'react';
import { withStyles } from "@material-ui/core";
import Paper from '@material-ui/core/Paper'
import Typography from "@material-ui/core/Typography";
import Grid from "@material-ui/core/Grid";
import Fab from "@material-ui/core/Fab";
import DeleteIcon from '@material-ui/icons/Delete';
import ArrowDownwardIcon from '@material-ui/icons/ArrowDownward';
import ArrowUpwardIcon from '@material-ui/icons/ArrowUpward';


const styles = theme => ({
    '@global': {
        body: {
            backgroundColor: theme.palette.common.white,
        }
    },
    paper: {
        paddingTop: theme.spacing(1),
        padding: theme.spacing(1),
    },
    title: {
        flexGrow: 1,
    },
    fab: {
        margin: theme.spacing(1),
    },
    extendedIcon: {
        marginRight: theme.spacing(1),
    },
});

class Film extends React.Component{
    constructor(props) {
        super(props);
        this.state = {
            extended: false,
        }
    }
    handlerButtonExtend = () => {
        const { extended } = this.state;
        this.setState({
            extended: !extended
        })
    };
    handlerButtonDelete = () => {
        this.props.handlerDeleteFilm(this.props.film.id);
    };
    drawInfo = () => {
        return (
            <div>
            {this.props.film.format && this.props.film.release_date ?
                (<Grid container justify="space-between">
                    <Typography>
                        Release date: {new Date(this.props.film.release_date).getFullYear()}
                    </Typography>
                    <Typography>
                        Format: {this.props.film.format}
                    </Typography>
                </Grid>)
                : ''
            }
            {this.props.film.hasOwnProperty('id') && this.props.film.actors ?
                (<Grid container justify="space-between">
                    <Typography>
                        Stars: {this.props.film.actors}
                    </Typography>
                    <Typography>
                        Id: {this.props.film.id}
                    </Typography>
                </Grid>)
                : ''
            }
            </div>)
    };
    render() {
        const { classes } = this.props;
        return (
            <Paper className={classes.paper}>
                <Grid container>
                    <Grid className={classes.title}>
                        <Typography variant="h5" component="h3">
                            {this.props.film.title}
                        </Typography>
                    </Grid>
                    <Grid>
                        <Fab size="small" aria-label="delete" className={classes.fab}
                            onClick={this.handlerButtonDelete}>
                            <DeleteIcon />
                        </Fab>
                        <Fab color="primary" size="small" variant="extended" aria-label="delete" className={classes.fab} onClick={this.handlerButtonExtend}>
                            {this.state.extended ? <ArrowUpwardIcon/> : <ArrowDownwardIcon/>}
                        </Fab>
                    </Grid>
                </Grid>
                {this.state.extended ? this.drawInfo() : ''}
            </Paper>)
    }
}

export default withStyles(styles)(Film);
